import '@babel/runtime-corejs3/core-js-stable/instance/index-of';

import { detection as detect } from '@accedo/xdk-core';

const isAndroidTV = () => {
    const ua = navigator.userAgent.toLowerCase();
    const androidtv = ua.indexOf('android') !== -1;

    if (androidtv) {
        // Detect different variations of Amazon FireTV, including Bravia AFTV user-agent, in order to discard the device as a typical Android TV device.
        // "Mozilla/5.0 (Linux; Android 5.1.1; AFTM Build/LVY48F) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/34.77.34.5 Mobile Safari/537.36",
        // "Mozilla/5.0 (Linux; Android 6.0.1; BRAVIA 4K 2015 Build/MMB29V.S31) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/34.77.34.5 Mobile Safari/537.36"
        const braviaAFTVRegex = /bravia\s*([a-zA-z\s*0-9]{1,})\s*2015\s*build\/mmb([^;)]+)/;
        const isBraviaATFV = ua && braviaAFTVRegex.test(ua);
        const isFireTV = ua.indexOf('aft') !== -1 || isBraviaATFV;
        if (isFireTV) {
            return false;
        }

        // eslint-disable-next-line no-console
        console.log('XDK Device detection successful, initializing AndroidTV device abstraction.');
    }
    return androidtv;
};

const detection = detect(isAndroidTV);

export default detection;
